#navbar{
    height: 80px;
    display: flex;
    position: fixed;
    top: 0px;
    width: calc(100% - 100px);
    z-index: 100;
    background-color: #262626;
    align-items: center;
    justify-content:flex-start;
    font-size: 18px;
    text-transform: uppercase;

    .nav-options{
        display: flex;
        align-items: center;
        width: 100%;

        .SearchInput-container{
            height: 30px;
        }

        ul {
            list-style: none;
            display: flex;
            height: 100%;
            width: 50%;
            align-items: center;
    
            li {
                margin-left: 45px;
                transition: all 1s;
                cursor: pointer;
    
                &:hover {
                    a{
                        color: #b6b6b6;
                    }
                }
    
                a{
                    text-decoration: none;
                    color: inherit;
                }
    
                span{
                    &:hover{
                        color: #b6b6b6;
                    }
                }
            }
    
            #palettes:hover{
                color: #2A7AE4;
            }
        }
    
        #extras-ul{
            .authPermissions {
                display: flex;
                justify-content: space-between;
            }
    
            button {
                width: 100px;
                border: none;
                background: none;
                color: inherit;
                font-size: 18px;
                text-transform: uppercase;
                cursor: pointer;
            }
            display: flex;
            justify-content: flex-end;
        }
    }

    
}

/* phones */

@media only screen and (max-width: 600px) {
    #navbar{
        flex-direction: column;
        height: 100vh;
        width: 100vw;
        justify-content: center;
        position: fixed;
        top: 0;
        right: 100%;
        transition: all 1s;

        &.active{
            right: 0;
        }

        #close-button{
            position: absolute;
            top: 30px;
            right: 40px;
        }

        .nav-options{
            flex-direction: column;

            #extras-ul{
                width: 200px;
                align-items: center;
                justify-content: center;

                li{
                    width: 100%;
                }

                .authPermissions{
                    flex-direction: column;
                    align-items: left;

                    button{
                        padding: 0;
                        width: 100%;
                    }
                }
            }

            ul {
                flex-direction: column;
                width: 100%;
                align-items: center;
                justify-content: center;
                padding: 0;

                li {
                    margin-top: 40px;
                    margin-left: 0;
                }
            }
        }
    }   
}

@media only screen and (min-width: 600px){
    #navbar{
        #close-button{
            display: none;
        }
    }
}
