.promotionInfoCard{
    width: 20%;
    height: 300px;

    img {
        width: 100%;;
    }

    .promotionalCard_logos{
        display: flex;
        overflow: hidden;

        img {
            object-fit: contain;
            position: relative;
            transition: all .1s;
        }
    }

    .promotionInfoCard-text{
        display: flex;
        flex-direction: column;
        text-align: left;
        margin-top: 10px;

        h4{
            color: #f0f0f0;
            margin-bottom: 0;
        }
    }
}

@media only screen and (max-width: 600px){
    .promotionInfoCard{
        width: 100%;
        display: flex;
        justify-content: center;
        margin: 20px 0;

        .promotionalCard_logos{
            margin: 0;
            padding: 0;
            width: 70%;
            overflow: hidden;
        }

        .promotionInfoCard-text{
            display: none;
        }
    }
}