.App {
  text-align: center;
  padding: 20px 50px 30px;
  overflow: hidden;

  .modal-button{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    width: 92%;
    height: 70px;
    z-index: 100;
    top: 0;
    left: 0px;
    padding: 0px 20px;
    background-color: #262626;

    svg{
      font-size: 25px;
    }
    
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@media only screen and (min-width: 601px){
  .App{
    .modal-button{
      display: none ;
    }
  } 
}

