.productCard{
    margin: 20px;
    margin-top: 40px;
    width: 230px;
    background-color: rgba(0,0,0,0.226);
    height: 300px;
    border-radius: 20px;
    position: relative;

    a.readMore{ 
        p{
            font-size: 16px;
            margin-top: 10px;
        }
        text-decoration: none;
        color: inherit;
    }

    img{
        width: 200px;
        height: 150px;
        position: relative;
        top: -50px;
        object-fit: contain;
        transition: all 1s;
        &:hover {
            transform: scale(1.2);
        }
    }

    p{
        font-size: 20px;
        margin-top: 0;
        padding: 5px;
    }

    .cardPrice{
        p{
            font-size: 16px;
            margin: 0;

            span{
                color: red;
                font-size: 13px;
                text-decoration: line-through;
                margin: 0;
            }
        }
    }
}