.SearchInput-container{
    width: 50%;
    display: flex;
    justify-content: flex-start;
    margin-left: 30px;
    position: relative;

    #search-input{
        margin: 0;
        width: 200px;
        background-color: transparent;
        border:1px solid #7e7e7e;
        border-radius: 10px;
        color: inherit;
        text-align: center;
    }
}

@media only screen and (max-width: 600px){
    .SearchInput-container{
        width: 100%;
        align-items: center;
        justify-content: center;
        margin-left: 0;
        margin-top: 40px;
    }
}   