#aboutUsSection{
    margin-top: 70px;
    width: 100vw;
    height: 500px;
    position: relative;
    left: -60px;
    display: grid;
    grid-template-columns: 30% 30% 30%;
    background-color: rgba(0,0,0,0.226);

    .logo{
        justify-content: center;
    }

    .aboutUsSection_Links{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        a{
            margin: 15px;
            color: inherit;
            text-decoration: none;
        }
    }

    .aboutUsSection_form{
        display: flex;
        align-items: center;

        p{
            text-align: left;
        }
        form{
            display: flex;
            flex-direction: column;

            input,textarea,button{
                background-color: #262626;
                border: none;
                margin-top: 15px;
            }
            input[type='text']{
                width: 500px;
                height: 60px;
                text-align: left;
            }
            textarea{
                height: 120px;
                resize: none;
            }

            button {
                width: 150px;
                height: 40px;
                background-color: #2A7AE4;
            }
        }
    }
}

@media only screen and (max-width: 600px){
    #aboutUsSection{
        flex-direction: column;
        height: 110vh;
        width: 140%;

        grid-template-columns: 100%;
        grid-template-rows: 10% 40% 50%;

        .aboutUsSection_form{
            text-align: center;
            align-items: center;
            justify-content: center;
            p{
                text-align: left;
            }

            form{
                display: flex;
                flex-direction: column;
    
                input,textarea,button{
                    background-color: #262626;
                    border: none;
                    margin-top: 15px;
                }
                input[type='text']{
                    width: 300px;
                    height: 60px;
                    text-align: left;
                }
                textarea{
                    height: 100px;
                    resize: none;
                }
    
                button {
                    width: 120px;
                    height: 40px;
                    background-color: #2A7AE4;
                }
            }
        }
    }
    
}