#viewProduct{
    .view{
        display: flex;
        
        img {
            width: 500px;
            max-height: 500px;
            margin-right: 50px;
        }
    }
    margin-top: 100px;

    .viewProduct-text{
        text-align: left;

        .cardPrice{
            p{
                font-size: 16px;
                margin: 0;

                span{
                    color: red;
                    font-size: 13px;
                    text-decoration: line-through;
                }
            }
        }
    }
}

@media only screen and (max-width: 600px){
    #viewProduct{
        .view{
            flex-direction: column;

            img{
                width: 100%;
            }
        }

        .similarProductsCardsContainer{
            position: relative;
            left: -40px;
            align-items: center;
            width: 125%;
            height: 400px;
            flex-wrap: nowrap;
            overflow: scroll;
        }
    }
}