#admin-panel{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h1{
        text-align: left;
    }

    form {
        display: flex;
        flex-direction: column;

        .addImg{
            display: flex;
            align-items: center;
            justify-content: center;

            .dragImg-box{
                background-color: rgba(0, 0, 0, 0.226);
                height: 200px;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                padding: 30px;
                margin-right: 18px;
                border: 1px solid #7e7e7e;
            }

            #inputFileNewImg{
                margin-left: 18px;
                width: 250px;
                background-color: rgba(0, 0, 0, 0.226);
            }
        }

        input[type='text']{
            width: 400px;
            height: 50px;
            margin-top: 28px;
            background-color: rgba(0, 0, 0, 0.226);
            border: 1px solid #7e7e7e;
            color: inherit;
            border-radius: 10px;
        }

        textarea{
            margin-top: 28px;
            background-color: rgba(0, 0, 0, 0.226);
            color: inherit;
        }
    }
}

@media only screen and (max-width: 600px){
    #admin-panel{
        width: 100%;

        h1{
            text-align: center;
        }

        form{
            width: 90%;
            align-items: center;

            .addImg{
                flex-direction: column;
                .dragImg-box{
                    width: 80%;
                    margin: 0;
                    margin-bottom: 10px;

                }

                #inputFileNewImg{
                    margin: 0;
                    margin-top: 10px;
                    width: 100%;
                }
            }

            input[type='text']{
                width: 100%;
            }
            textarea{
                width: 100%;
            }
        }
    }
}