#headerPromotion{
    display: flex;
    width: 100%;
    height: 100vh;
    align-items: center;
    justify-content: space-between;
    position: relative;
}

.slider-promotionalImgs{
    display: flex;
    height: 520px;
    width: 550px;
    overflow: hidden;
    position: relative;

    #Image-Promotion{
        min-width: 100%;
        height: 100%;
        object-fit: contain;
        position: relative;
        transition: all .1s;
    }
}

@media only screen and (max-width: 600px){
    #headerPromotion{
        flex-direction: column-reverse;
        margin-top: 50px;
    }
}